.card {
  color: white;
  position: relative;
  width: 100vw;
  height: 100vh;
}
.copy,.nav,.card img {
  position: absolute;
}
.card img {
  z-index: -10;
  object-fit: cover;
  width: 100vw;
  max-height: 100vh;
}
.nav {
  color: white;
  width: 100%;
  height: 20px;
  padding: 100px 200px 200px 100px;
  top:0;
  left: 0;
  background-image: url("images/GradientBG2.png");
  background-size: 100% 100%;
}
.copy {
  width: 50%;
  padding: 200px 200px 100px 100px;
  left: 0px;
  bottom: 0px;
  background-image: url("images/GradientBG.png");
  background-size: 100% 100%;
}
h1,p {
  position: static;
  padding: 0;
  margin: 0;
}
h1 {
  font-size: 36pt;
}
p {
  font-size: 24pt;
  font-weight: 200;
}